.search-form {
  background-color: #aa1389;
  justify-content: center;
  align-items: center;
  padding: 15px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 1px rgba(35, 27, 29, .04), 0 1px rgba(255, 255, 255, .25);
}

input {
  box-sizing: border-box;
  cursor: text;
  border: 1px solid rgba(27, 31, 35, .15);
  border-radius: 4px;
  margin-right: 10px;
  padding: 10px;
  font-size: 18px;
  list-style: none;
  display: inline-block;
  box-shadow: 0 1px rgba(27, 31, 35, .04), 0 1px rgba(255, 255, 255, .25);
}

button {
  box-sizing: border-box;
  cursor: pointer;
  background-color: #fafbfc;
  border: 1px solid rgba(27, 31, 35, .15);
  border-radius: 4px;
  padding: 12px;
  font-size: 13px;
  list-style: none;
  display: inline-block;
  box-shadow: 0 1px rgba(27, 31, 35, .04), 0 1px rgba(255, 255, 255, .25);
}

img {
  width: 100%;
  height: 200px;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin-top: -30px;
  padding-top: 30px;
  display: flex;
  position: absolute;
  top: 60px;
}

.photo-card {
  flex-basis: calc(25% - 30px);
  margin-top: 30px;
  margin-right: 30px;
}

.photo-card:nth-of-type(4n) {
  margin-right: 0;
}

.info {
  border: 1px solid rgba(27, 31, 35, .15);
  border-top: none;
  border-radius: 0 0 4px 4px;
  justify-content: space-around;
  align-items: center;
  display: flex;
  box-shadow: 0 1px rgba(27, 31, 35, .04), 0 1px rgba(255, 255, 255, .25);
}

.info-item, .span-item {
  text-align: center;
  display: block;
}

